import { ready } from "./util";
/**
 * cdn
 * 
 * 1.cdn downcdn
 * 2.cdn()
 * 3.
 * cdn
 * TODO: fallback
 */
const STORAGE_KEY = 'sakurairo_prefer_cdn';
const CDN_LIST = [
    /*     ["https://jscdn.host/release/ucode/", "/", "/", ""],
        ["https://cdnjs.cloudflare.com/ajax/libs/", "/", "/", ""],
     */
    ["https://jsd.nmxc.ltd/npm/", "@", "/", ""],
    ["https://cdn.jsdelivr.net/npm/", "@", "/", ""],
    ["https://unpkg.com/", "@", "/", ""],
    //TODO:cdn
];
function getPreferCDNIndex() {
    const config = localStorage.getItem(STORAGE_KEY);
    if (config) {
        const num = parseInt(config);
        if (isNaN(num) || num >= CDN_LIST.length) {
            //
            localStorage.removeItem(STORAGE_KEY);
            return 0;
        }
        return num;
    }
    else {
        return 0;
    }
}
const currentCDNIndex = getPreferCDNIndex();
const currentCDN = { raw: CDN_LIST[currentCDNIndex] };
/**
 * CDNnpm
 * @author KotoriK
 * @param relativePath npm
 * @param packageName npm
 * @param version npmlatest
 * @returns cdnurl
 */
export const resolvePath = (relativePath, packageName, version) => String.raw(currentCDN, packageName, version || PKG_INFO[packageName] || 'latest', relativePath);
const resolvePathByCDN = (cdn, relativePath, moduleName, version) => String.raw({ raw: cdn }, moduleName, version, relativePath);
function isServedByCurrentCDN(path) {
    const cdnMatchKey = CDN_LIST[currentCDNIndex][0];
    if (path.match(cdnMatchKey)) {
        return true;
    }
}
function analyze(time = 30000) {
    let failedCounts = 0;
    let totalCounts = 0;
    const observer = new PerformanceObserver((list, observer) => {
        for (const entry of list.getEntries()) {
            if (isServedByCurrentCDN(entry.name)) {
                totalCounts++;
                const { transferSize } = entry;
                if (transferSize == 0) {
                    //
                    failedCounts++;
                }
            }
        }
    });
    observer.observe({ entryTypes: ['resource'] });
    setTimeout(() => {
        observer.disconnect();
        if (failedCounts / totalCounts > 0.7) {
            //cdn
            const nextCDN = currentCDNIndex + 1;
            localStorage.setItem(STORAGE_KEY, (nextCDN >= CDN_LIST.length ? 0 : nextCDN).toString());
        }
    }, time);
}
ready(analyze);
